.app {
    font-size: 1.6rem;
    @media screen and (max-width: 960px) {
        font-size: 1.4rem;
    }

    .aside {
        position: fixed;
        left: 0;
        height: 100vh;
        overflow-y: auto;

        .logo {
            height: 3rem;
            margin: 1rem;
            box-sizing: border-box;
            border-radius: 1rem;
            padding: 0 1rem;
            text-align: center;

            img {
                height: 100%;
            }
        }
    }

    .header {
        background-color: #fff;
        padding: 0 1.5rem;
        display: flex;
        justify-content: space-between;

        .right {
            display: flex;
            align-items: center;
        }
    }

    .content {
        padding: 10px 18px;
        width: auto !important;
    }

    .footer {
        text-align: center;
    }
}

// 仅支持 chrome 滚动条样式

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
    width: 5px;
    background-color: #fff;
}

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #ccc;
}

.base-style {
    background-color: #fff;
    margin-bottom: 1rem;
    padding: 1.5rem;
    border-radius: 5px;
    box-shadow: 0px 2px 13px 0px rgba(228, 228, 228, 0.6);
}